import React from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  CircularProgress,
} from '@material-ui/core';
import Country from '../models/country';
import ReportLineChart from './report-linechart';
import ReportBarChart from './report-barchart';
import './countries-statistics.css';
import { CountryDailyReport } from '../interfaces';
export interface CountryStatsProps {
  country: Country;
  open: boolean;
  onClose: any;
  report: CountryDailyReport[];
}
export default function CountryStats({
  country,
  open,
  onClose,
  report,
}: CountryStatsProps) {
  return (
    <div>
      <Dialog
        open={open}
        aria-labelledby="responsive-dialog-title"
        onClose={onClose}
        fullWidth={true}
        maxWidth="sm"
      >
        <DialogTitle
          id="responsive-dialog-title"
          className="bg-gray-200 mb-4  flex justify-center"
        >
          <div className="flex items-center">
            <img
              className="w-4 h-4 rounded-full mr-4"
              src={`${country.countryInfo?.flag}`}
              alt=""
            />
            <span className="font-bold">{country.country}</span>
          </div>
        </DialogTitle>
        <DialogContent>
          {report.length ? (
            <>
              <ReportLineChart report={report} scale="log" />

              <ReportLineChart report={report} scale="linear" />
              <ReportBarChart report={report} field="todayCases" />
              <ReportBarChart report={report} field="todayDeaths" />
            </>
          ) : (
            <span>
              <CircularProgress color="primary" size={20} /> Loading country
              data
            </span>
          )}
        </DialogContent>
        <DialogActions>
          <Button autoFocus color="primary" onClick={onClose}>
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
