import React, { useState, useEffect } from 'react';
import CountryCard from '../components/country-card';
import PropertiesSorter, {
  PropertiesSorterProps,
} from '../components/country-properties-sorter';
import Country from '../models/country';
import './countries-container.css';
import { CircularProgress, FormControlLabel, Switch } from '@material-ui/core';
import CountriesTable from '../components/countries-table';
import ViewSelector from '../components/view-selector';
import { CountryProps, CountryDailyReport } from '../interfaces';
import {
  getDailyCountriesReport,
  getHistoricalReport,
} from '../services/dataProvider';
import CountryStats from '../components/country-statistics';
import isMobile from 'is-mobile';
import CountriesComparatorLines from '../components/countries-comparator-lines';
import {
  countrySorterOptions,
  countryCompareOptions,
} from '../services/select-options';

//module variable
let historicalReport: { [key: string]: CountryDailyReport[] } = {};
export default function CountriesContainer() {
  //__STATE HOOKS

  const [countries, setCountries] = useState<Country[]>([]);
  const [selectedCountries, setSelectedCountries] = useState<string[]>([]);
  const [selectedCountry, setSelectedCountry] = useState<Country>();

  const [sortProperties, setSortProperties] = useState(countrySorterOptions);

  //hook initialized with last access to page data is
  const [previousCountries, setPreviousCountries] = useState<Country[]>(
    (JSON.parse(localStorage.getItem('countries') || '[]') as Country[]) || []
  );
  const [sortProperty, setSortProperty] = useState<string>('cases');
  const [view, setView] = useState<'cards' | 'table'>(
    isMobile() ? 'cards' : 'table'
  );
  const [filterFlag, setFilterFlag] = useState<boolean>(false);
  const [compareFlag, setCompareFlag] = useState<boolean>(false);

  //__EFFECT HOOK | FETCH THE DATA
  useEffect(() => {
    const getCountries = async () => {
      try {
        const newData = await getDailyCountriesReport(1000, 'cases');

        //if no error, store  in local storage

        const countriesString = JSON.stringify(newData);

        //countries field in local storage
        localStorage.setItem('countries', countriesString);

        //set state of countries
        setCountries(newData as Country[]);

        //save the historical; does not trigger render
        const countriesPopulation = newData.reduce((acc, curr) => {
          acc[curr.country] = curr.getPopulation;
          return acc;
        }, {} as any);
        historicalReport = await getHistoricalReport(countriesPopulation);
      } catch (error) {
        setCountries(previousCountries);
        console.log(error);
      }
    };

    //fetch countries data
    getCountries();
  }, []);

  //__ SORT
  const handleSort = (property: CountryProps = 'cases') => {
    // sorts by descendig by field
    const sortByProperty = (a: Country, b: Country): 1 | -1 | 0 => {
      if (b[property]! > a[property]!) return 1;
      if (b[property]! < a[property]!) return -1;
      return 0;
    };

    var sortedNewCountries = countries.slice().sort(sortByProperty);
    //ranking according the property ascending
    sortedNewCountries.forEach((c, i) => (c.ranking = i + 1));

    var sortedPreviousCountries = previousCountries
      .slice()
      .sort(sortByProperty);

    //store state
    setSortProperty(property);
    setCountries(sortedNewCountries);
    setPreviousCountries(sortedPreviousCountries);
  };

  //__ VIEW TYPE: cards | table
  const handleChangeView = (view: 'cards' | 'table') => {
    setView(view);
  };

  //__ ADD COUNTRY TO THE LIST OF SELECTED COUNTRIES
  const handleSelectCountry = (countryName: string) => {
    let newSelectedCountries = selectedCountries.slice();
    const position = newSelectedCountries.indexOf(countryName);
    if (position !== -1) {
      newSelectedCountries.splice(position, 1);
    } else {
      newSelectedCountries.push(countryName);
    }
    if (newSelectedCountries.length === 0) {
      setFilterFlag(false);
    }
    setSelectedCountries(newSelectedCountries);
  };

  //__ SELECT A COUNTRY
  const handleShowCountryStatistics = (country?: Country) => {
    setSelectedCountry(country);
  };

  //__ UNSELECT THE COUNTRY
  const handleCloseCountryStats = () => {
    setSelectedCountry(undefined);
  };

  //__ SWITCH ON THE FILTER
  const toggleFilterFlag = (e: any, filter: boolean) => {
    setFilterFlag(filter);
  };
  const handleCompareChange = (isCompareActive: boolean) => {
    setCompareFlag(isCompareActive);
    setSortProperty('deathsPerOneMillion');
    isCompareActive
      ? setSortProperties(countryCompareOptions)
      : setSortProperties(countrySorterOptions);
  };

  //data is going to be displayed; filtered or not
  let dataToRender = countries || previousCountries;
  const average = getTruncatedAverage(dataToRender);

  //filtered data
  if (filterFlag) {
    dataToRender = dataToRender.filter((country) =>
      selectedCountries.includes(country.country || '')
    );
  }
  return (
    <>
      <div
        className="flex mb-2   p-2 flex-wrap controls-menu"
        style={{ backgroundColor: 'rgba(0, 0, 0, 0.04)' }}
      >
        <PropertiesSorter
          onSort={handleSort}
          items={sortProperties}
          selectedProperty={sortProperty}
        />
        <ViewSelector view={view} onChangeView={handleChangeView} />

        {/*//__SWITCH TO ACTIVATE THE  FILTER */}
        <span className="ml-4 flex">
          <FormControlLabel
            control={
              <Switch
                name="checkedA"
                size="small"
                checked={filterFlag}
                onChange={toggleFilterFlag}
                disabled={selectedCountries.length === 0}
              />
            }
            label="Filter"
          />
        </span>

        {/*//__SWITCH TO ACTIVATE THE  FILTER */}
        <span className="ml-4 flex">
          <FormControlLabel
            control={
              <Switch
                size="small"
                checked={compareFlag}
                onChange={(e, value) => handleCompareChange(value)}
                disabled={selectedCountries.length === 0}
              />
            }
            label="Compare"
          />
        </span>
      </div>

      {/*//__ MODAL WITH COUNTRY STATS */}
      {selectedCountry && (
        <CountryStats
          country={selectedCountry}
          open={selectedCountry !== undefined}
          onClose={handleCloseCountryStats}
          report={historicalReport[selectedCountry.country]}
        />
      )}
      {/*//__ SPINNER */}
      {dataToRender?.length === 0 && (
        <div className="flex justify-center items-center">
          <CircularProgress color="primary" size={20} />
          <span className="ml-2">Loading</span>
        </div>
      )}

      {/*//__ COMPARATOR */}
      {compareFlag && selectedCountries.length > 0 && (
        <CountriesComparatorLines
          property={sortProperty}
          report={historicalReport}
          selectedCountries={selectedCountries}
        />
      )}
      {/*//__ COUNTRIES  RENDERED */}
      {!compareFlag && view === 'cards' ? (
        <div className="grid md:grid-cols-3 gap-4  xl:grid-cols-3">
          {dataToRender?.map((country) => (
            <CountryCard
              onSelectCountry={handleShowCountryStatistics}
              country={country}
              key={country.country}
              activeProperty={sortProperty as CountryProps}
              previousData={previousCountries.find(
                (c) => c.country === country.country
              )}
            />
          ))}
        </div>
      ) : (
        !compareFlag &&
        dataToRender.length !== 0 && (
          <CountriesTable
            onShowStats={handleShowCountryStatistics}
            average={average}
            onSelectCountry={handleSelectCountry}
            countries={dataToRender}
            sortProperty={sortProperty as CountryProps}
            selectedCountries={selectedCountries}
          />
        )
      )}
    </>
  );
}
const getTruncatedAverage = (countries: Country[]) => {
  const properties: CountryProps[] = [
    'cases',
    'todayCases',
    'deaths',
    'todayDeaths',
    'lethality',
    'casesPerOneMillion',
    'deathsPerOneMillion',
    'tests',
    'testsPerOneMillion',
  ];
  let truncatedAverage: { [key: string]: number } = {};

  properties.forEach((property) => {
    const truncated = countries.map((c: any) => c[property] as number).sort();
    if (countries.length > 10) {
      truncated.pop();
      truncated.pop();
      truncated.shift();
      truncated.shift();
    }

    const sum = truncated.reduce((a, b) => a + b, 0);
    const average = sum / countries.length;
    const result = Math.round(average);
    truncatedAverage[property] = result;
  });

  return truncatedAverage;
};
