import React from 'react';

import {
  XAxis,
  YAxis,
  Bar,
  BarChart,
  Tooltip,
  ResponsiveContainer,
  Legend,
  ReferenceLine,
} from 'recharts';
interface ReportBarchartProps {
  report: {
    date: string;
    cases: number;
    deaths: number;
    todayCases: number;
    todayDeaths: number;
  }[];
  field: 'todayCases' | 'todayDeaths';
}
const deathsColor = '#F44336';
const casesColor = 'rgb(0, 136, 254)';
export default function ReportBarChart({ report, field }: ReportBarchartProps) {
  const data = report.slice(-30);
  const average =
    data.map((a) => a[field]).reduce((a, b) => a + b) / data.length;
  return (
    <div className="mb-4">
      <h3 className="bg-gray-100 p-2 mb-2 font-bold">
        {field.replace('today', '')} per day in the last month{' '}
        <span
          className={`font-normal px-1 rounded ${
            field === 'todayDeaths' && average > 300
              ? average > 600
                ? 'bg-red-800 text-white'
                : 'bg-red-500 text-white'
              : 'bg-gray-300'
          }`}
        >
          average {Math.round(average)}
        </span>
      </h3>
      <div
        style={{
          width: '100%',
          height: 200,
          maxWidth: 500,
          padding: 10,
          color: 'white',
          margin: '0 auto',
          backgroundColor: 'rgba(0,0,0,.9)',
          borderRadius: 10,
        }}
      >
        <ResponsiveContainer>
          <BarChart data={data} height={200} width={500}>
            <Tooltip content={<CustomTooltip />} />
            <Legend />
            <XAxis dataKey="date" stroke="darkgray" />
            <YAxis dataKey={field} stroke="darkgray" />

            <Bar
              name={field.replace('today', '').toLowerCase()}
              dataKey={field}
              fill={field === 'todayCases' ? casesColor : deathsColor}
            />
            <ReferenceLine
              y={average}
              stroke="yellow"
              strokeDasharray="3 3"
              isFront={true}
            />
          </BarChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}
const CustomTooltip = ({ active, payload, label }: any) => {
  if (active) {
    return (
      <div className="bg-white text-black border-gray-100 p-2 border border-solid">
        <p>{label}</p>

        <p className="label">{`${payload[0].name}: ${payload[0].value}`}</p>
      </div>
    );
  }

  return null;
};
