import React from 'react';
import pick from 'lodash.pick';
import { CountryDailyReport } from '../interfaces';
import {
  LineChart,
  Line,
  XAxis,
  Tooltip,
  Legend,
  YAxis,
  ResponsiveContainer,
  CartesianAxis,
  CartesianGrid,
} from 'recharts';
import { colors } from '../services/colors';

interface CountriesComparatorLinesProps {
  report: { [contry: string]: CountryDailyReport[] };
  selectedCountries?: string[];
  property: string;
}

export default function CountriesComparatorLines({
  report,
  selectedCountries,
  property,
}: CountriesComparatorLinesProps) {
  const compareArr = mapData(report, property, selectedCountries);

  return (
    <>
      <h3 className="font-bold p-5">
        Comparing{' '}
        <span style={{ color: '#FF5722' }}>
          {property.replace(/([a-z])([A-Z])/g, '$1 $2').toLowerCase()}
        </span>
        {selectedCountries?.map((c, i) => {
          return (
            <span
              style={{
                marginLeft: 10,
                padding: '1px 10px',
                borderRadius: '2em',
                backgroundColor: colors[i],
                color: 'white',
              }}
            >
              {c}
            </span>
          );
        })}
      </h3>
      <br />
      <div style={{ width: '100%', height: '50vh' }}>
        <ResponsiveContainer>
          <LineChart data={compareArr}>
            {selectedCountries?.map((c, i) => {
              return <Line dataKey={c} key={c} stroke={colors[i]} />;
            })}
            <XAxis dataKey="date" />
            <YAxis />
            <Tooltip />
            <Legend />
            <CartesianGrid strokeDasharray="1 1" />
          </LineChart>
        </ResponsiveContainer>
      </div>
    </>
  );
}

function mapData(
  report: { [contry: string]: CountryDailyReport[] },
  property: string,
  selectedCountries?: string[]
) {
  //just the selected countries reports
  if (selectedCountries?.length) {
    report = pick(report, [...selectedCountries, 'date']);
  }

  let compareObject: any = {};

  //group all countries by date
  Object.entries(report).map(([country, report]) => {
    {
      report.forEach((r) => {
        const countryData: any = r[property as keyof CountryDailyReport];
        const date = r['date'];
        compareObject[date] = {
          ...compareObject[date],
          [country]: countryData,
        };
      });
    }
  });

  //delete empty entries
  Object.entries(compareObject).forEach(([date, countryOb]) => {
    Object.entries(countryOb as any).forEach(([key, value]) => {
      if (value === 0) {
        delete compareObject[date][key];
      }
      if (Object.keys(compareObject[date]).length === 0) {
        delete compareObject[date];
      }
    });
  });

  //map the object to an array
  let compareArr = Object.entries(compareObject)
    .map(([date, value]) => {
      return {
        date,
        ...(value as {}),
      };
    })
    .sort((a, b) => (a.date > b.date ? 1 : -1));

  return compareArr;
}
