import React from 'react';
import {
  LineChart,
  XAxis,
  YAxis,
  CartesianGrid,
  Legend,
  Line,
  Tooltip,
  ResponsiveContainer,
} from 'recharts';
import { colors } from '../services/colors';

interface ReportProps {
  report: { date: string; cases: number; deaths: number }[];
  scale: 'log' | 'linear';
}

const deathsColor = '#F44336';
const casesColor = 'rgb(0, 136, 254)';
export default function ReportLineChart({ report, scale }: ReportProps) {
  return (
    <div className="mb-4">
      <h3 className="bg-gray-100 p-2 mb-2 font-bold">
        Evolution of cases and deaths since {report[0].date}{' '}
        <span className="bg-yellow-400 px-2 font-normal rounded-lg">
          {scale} scale
        </span>
      </h3>
      <div
        style={{
          width: '100%',
          height: 200,
          maxWidth: 500,
          padding: '10px 10px 0',
          color: 'white',
          margin: '0 auto',
          backgroundColor: 'rgba(0,0,0,.9)',
          borderRadius: 10,
        }}
      >
        <ResponsiveContainer>
          <LineChart data={report} barSize="big">
            <XAxis
              dataKey={'date'}
              allowDataOverflow={true}
              stroke="darkgray"
              type="category"
            />
            <YAxis
              dataKey="cases"
              scale={scale}
              domain={[1, 'auto']}
              stroke="darkgray"
            />
            <CartesianGrid strokeDasharray="1 1" />
            <Tooltip />
            <Legend />
            <Line
              type="monotone"
              dataKey="cases"
              stroke={'rgba(0, 136, 254, 0.82)'}
              dot={{ r: 3, fill: casesColor }}
            />{' '}
            <Line
              type="linear"
              dataKey="deaths"
              stroke={'red'}
              dot={{ r: 3, fill: deathsColor }}
            />
          </LineChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}
