import Country from '../models/country';
import {
  CountryProps,
  HistoricalCountry,
  CountryDailyReport,
} from '../interfaces';

//__ INITIAL CURRENT COUNTRIES DATA
export async function getDailyCountriesReport(
  minCases: number,
  sortProperty: CountryProps
) {
  const countriesUrl = 'https://corona.lmao.ninja/v2/countries';
  let countriesJson: Country[] = [];

  try {
    countriesJson = await (await fetch(countriesUrl)).json();
    const countries = countriesJson
      .filter((c) => c.cases > minCases)
      .sort((a, b) => b[sortProperty] - a[sortProperty])
      .map((c) => {
        return Object.assign(new Country(), c);
      });

    countries.forEach((c, i) => (c.ranking = i + 1));

    return countries;
  } catch (error) {
    console.log(error);
    return [];
  }
}

//__ HISTORICAL COUNTRIES DATA
export async function getHistoricalReport(countriesPopulation: {
  [country: string]: number;
}): Promise<{
  [key: string]: CountryDailyReport[];
}> {
  const countryUrl = `https://corona.lmao.ninja/v2/historical/?lastdays=95`;

  const countryJson = (await (
    await fetch(countryUrl)
  ).json()) as HistoricalCountry[];
  countryJson.map((c) => c.timeline);

  const historical: { [key: string]: HistoricalCountry } = {};

  countryJson.forEach((currentCountry) => {
    const accumulatedCountry = historical[currentCountry.country];
    if (accumulatedCountry) {
      sumProvinces(currentCountry, accumulatedCountry, 'cases');
      sumProvinces(currentCountry, accumulatedCountry, 'deaths');
    } else {
      historical[currentCountry.country] = currentCountry;
    }
  });
  let report: { [key: string]: CountryDailyReport[] } = {};

  Object.entries(historical).forEach(([countryName, historicalCountry]) => {
    historicalCountry['population'] = countriesPopulation[countryName];
    report[countryName] = mapToCountryReport(historicalCountry);
  });

  return report;
}

//__UTIL METHODS TO MAP DATA

//sum all provinces into one country
//mutates accumulatedCountry
function sumProvinces(
  currentCountry: HistoricalCountry,
  accumulatedCountry: HistoricalCountry,
  property: 'deaths' | 'cases'
) {
  let newOccurrences = currentCountry.timeline[property];
  let accumulatedOccurrences = accumulatedCountry.timeline[property];
  Object.entries(newOccurrences).forEach(([key, value]) => {
    if (accumulatedOccurrences[key]) {
      accumulatedOccurrences[key] += value;
    } else {
      if (value !== 0) {
        accumulatedOccurrences[key] = value;
      }
    }
  });
}

//maps
function mapToCountryReport(country: HistoricalCountry): CountryDailyReport[] {
  const timeline = country.timeline;
  const cases = timeline.cases as {
    [key: string]: number;
  };
  const deaths = timeline.deaths as {
    [key: string]: number;
  };
  const days = Object.keys(timeline.cases)
    .map((k, i, arr) => {
      let [month, day] = k.split('/');
      if (month.length === 1) month = '0' + month;
      if (day.length === 1) day = '0' + day;
      const formattedDate = month + '-' + day;
      const acumulatedCases: number = cases[k];
      const acumulatedDeaths = deaths[k];

      //calculate today values, infering from previous
      let todayCases =
        i === 0 ? acumulatedCases : acumulatedCases - cases[arr[i - 1]];
      let todayDeaths =
        i === 0 ? acumulatedDeaths : acumulatedDeaths - deaths[arr[i - 1]];
      //calculate per one million
      const casesPerOneMillion = acumulatedCases
        ? Math.round(acumulatedCases / country.population)
        : 0;
      const deathsPerOneMillion = acumulatedDeaths
        ? Math.round(acumulatedDeaths / country.population)
        : 0;
      return {
        date: formattedDate,
        cases: acumulatedCases,
        casesPerOneMillion,
        todayCases,
        deaths: acumulatedDeaths,
        deathsPerOneMillion,
        todayDeaths,
      };
    })
    .filter((d) => d.cases !== 0);

  return days;
}

//given a month number, returns abbreviation for this month
const getMonthAbb = (x: number) => {
  const months = [
    'Jan',
    'Feb',
    'Mar',
    'Apr',
    'May',
    'Jun',
    'Jul',
    'Aug',
    'Sep',
    'Oct',
    'Nov',
    'Dec',
  ];
  const monthNumber = x - 1;
  return months[monthNumber];
};
