import React from 'react';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import './select.css';
import { CountryProps } from '../interfaces';
export interface PropertiesSorterProps {
  onSort: (property?: CountryProps, mode?: 'ASC' | 'DESC') => void;
  items: { text: string; value: string | undefined }[];
  selectedProperty: string;
}

export default function PropertiesSorter({
  onSort,
  items,
  selectedProperty,
}: PropertiesSorterProps) {
  return (
    <>
      <FormControl
        variant="filled"
        className="border-10 border"
        style={{ minWidth: 135 }}
      >
        <InputLabel id="select-label">Sort by</InputLabel>
        <Select
          labelId="select-label"
          id="select"
          onChange={(e) => onSort(e.target.value as CountryProps)}
          label="Sort by"
          value={selectedProperty}
        >
          {items.map((i) => {
            const result =
              i.value !== undefined ? (
                <MenuItem key={i.value} value={i.value}>
                  {i.text}
                </MenuItem>
              ) : (
                <br />
              );

            return result;
          })}
        </Select>
      </FormControl>
    </>
  );
}
