import React from 'react';

import CardProperty from './card-property';
import Country from '../models/country';
import './country-card.css';
interface CountryCardProps {
  country: Country;
  previousData?: Country;
  activeProperty: keyof Country;
  onSelectCountry: (country?: Country) => void;
}
export default function CountryCard({
  country,
  activeProperty,
  previousData,
  onSelectCountry,
}: CountryCardProps) {
  return (
    <div className="shadow rounded-md overflow-hidden hover:shadow-md  transition duration-300 ease-in-out ">
      {/* header */}
      <div
        onClick={(e) => onSelectCountry(country)}
        className="w-full cursor-pointer flex justify-center items-center p-5 font-bold uppercase text-black text-lg relative"
        style={{ backgroundColor: 'rgba(0,0,0,.04)' }}
      >
        <span className="absolute" style={{ left: '2rem' }}>
          {country.ranking}.
        </span>
        <img src={country.countryInfo?.flag} alt="" className="h-4 mr-2" />
        <h3>{country.country}</h3>

        {country.isNewData && (
          <div
            className="blob green"
            title="there is new data from the last time you visited the site"
          ></div>
        )}
      </div>
      {/* body */}
      <div className="px-6 py-2">
        <CardProperty
          text="Cases"
          currentValue={country.cases}
          isActive={activeProperty === 'cases'}
        />
        <CardProperty
          text="Today cases"
          currentValue={country.todayCases}
          previousValue={previousData?.todayCases}
          isActive={activeProperty === 'todayCases'}
        />
        <br className="my-3" />
        <CardProperty
          text="Deaths"
          currentValue={country.deaths}
          isActive={activeProperty === 'deaths'}
        />
        <CardProperty
          text="Today deaths"
          currentValue={country.todayDeaths}
          previousValue={previousData?.todayDeaths}
          isActive={activeProperty === 'todayDeaths'}
        />
        <br className="my-3" />
        <CardProperty
          text="Lethality"
          currentValue={country.lethality}
          isActive={activeProperty === 'lethality'}
        />
        <CardProperty
          text="Cases per 1M"
          currentValue={country.casesPerOneMillion}
          isActive={activeProperty === 'casesPerOneMillion'}
        />
        <CardProperty
          text="Deaths per 1M"
          currentValue={country.deathsPerOneMillion}
          isActive={activeProperty === 'deathsPerOneMillion'}
        />
        <br />
        <CardProperty
          text="Tests"
          currentValue={country.tests}
          isActive={activeProperty === 'tests'}
        />
        <CardProperty
          text="Tests per 1M"
          currentValue={country.testsPerOneMillion}
          isActive={activeProperty === 'testsPerOneMillion'}
        />
      </div>
    </div>
  );
}
